import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import Infos from '@/components/cards/infos/Infos'
import LLoading1 from '@/components/lLoading1/LLoading1'
export default {
  name: 'inscription1',
  components: {
    InputI0,
    ButtonI0,
    Infos,
    LLoading1,
  },
  data () {
    return {
      loading:false
    }
  },
  methods: {
    submiting (e) {
      e.preventDefault()
      return false
    },
    save () {
      if (!this.validation()) return
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'ins1',
        nom: this.$refs.surname.message,
        email: this.$refs.email.message,
        password: this.$refs.password.message
      })
        .then(({data}) => {
          this.loading = false
          if(data=='exist') alert('Sorry this email is taken')
          else this.$refs.infos.displaying(this.$refs.email.message)
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validateEmail (code) {
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'validateEmail',
        code: code
      })
        .then(({data}) => {
          this.loading = false
          if(data === 'success') {
            alert('Success')
            this.$router.push('/inscription2')
          }
          else alert('le code est incorrect')
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validation () {
      let regex1 = /^.{2,100}$/
      if (this.$refs.surname.message == '' ||
        !regex1.test(this.$refs.email.message)) {
        alert('nom ou email incorrect')
        return false
      }
      if (this.$refs.password.message == '') {
        alert('password incorrect')
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        alert('passwords did not match')
        return false
      }
      return true
    }
  }
}
