import InputSearch from '@/components/inputSearch/InputSearch'

export default {
    name: 'chooseCity',
    components: {
        InputSearch
    },
    methods: {
        stateChange (options) {
            if(options.state == 1) {
              this.$refs.villes.currIndex = options.word.index
            }
            this.$refs.villes.changeState()
        },
        validation () {
            if (this.$refs.Pays.state == 0) {
              alert('Veuillez choisir un pays')
              return false
            }
            if (this.$refs.villes.state == 0) {
              alert('Veuillez choisir une ville')
              return false
            }
            return true
        },
        toGuides() {
            if(this.validation()) this.$router.push({name:'guides', params: {pays: this.$refs.Pays.current, ville:this.$refs.villes.current }})
        }
    }
}